import Layout from "../components/Layout";

import styled from "styled-components";
import NextSeo from "next-seo";

import FacebookLike from "../components/FacebookLike";

const FacebookLikePageDiv = styled.div`
  background: white;
  height: 70vh;
  text-align: center;

  .likeF {
    overflow: hidden;
    margin-left: 39vw;
    margin-top: 8vh;

    @media only screen and (max-width: 767px) {
      margin-left: 10vw;
    }

    @media only screen and (max-width: 1200px) and (min-width: 767px) {
    }
  }

  .facebookLikeHeader {
  }
`;

function FacebookLikePage() {
  return (
    <Layout>
      <FacebookLikePageDiv>
        <NextSeo
          config={{
            title:
              "Reveler Music, LLC - #BringinTheParty ** Music Entertainment Services - FacebookLikePage Page",
            description:
              "Meet Reveler Music, LLC, we offer music entertainment services, such as, Live Music, Live Sound and Open Mics, Karaoke and DJ, to Pueblo, Colorado Springs, Castle Rock, Denver and the surrounding areas.",
            canonical: "https://revelermusic.com",
            openGraph: {
              type: "website",
              url: "https://revelermusic.com/facebookLivePage",
              title: "Reveler Music, LLC - FacebookLikePage Page",
              description:
                "Meet Reveler Music, LLC, we offer music entertainment services, such as, Live Music, Live Sound and Open Mics, Karaoke and DJ, to Pueblo, Colorado Springs, Castle Rock, Denver and the surrounding areas.",
              site_name: "Revler Music, LLC"
            },
            twitter: {
              handle: "@jwgravesco"
            }
          }}
        />
        <h1 className="facebookLikeHeader">
          Like or Share
          <br />
          Reveler Music
          <br />
          on Facebook
        </h1>
        <div className="likeF">
          <FacebookLike />
        </div>
      </FacebookLikePageDiv>
    </Layout>
  );
}

export default FacebookLikePage;
